import React from 'react';
import { Flex, Box, Heading, Text } from 'rebass';

import CatalogProducts from './CatalogProducts';
import Pagination from '../../components/Pagination';
import CollectionStats from '../../components/CollectionStats';

function CatalogPage(props) {
  const { limit, skip, cartUrl } = props.pageContext;
  const { title, description } = props.data.collection.nodes[0];
  let products = [];

  props.data.collection.nodes.forEach((node) => {
    products = [...products, ...node.products];
  });

  return (
    <Flex flexWrap="wrap" mx="auto" pt={["20vw", "15vw"]} sx={{ background: 'url("//cdn.shopify.com/s/files/1/0117/9005/7534/files/bg-face.jpg")' }}>
      <Flex
        width={1}
        flexWrap={['wrap', 'nowrap']}
        justifyContent="space-between"
        alignItems="flex-end"
      >
        <Box textAlign="center" py="2em" px={["2em", "6em", "14em"]}>
          <Text fontSize={["10vw", "5vw"]} fontWeight="body">{title}</Text>
          <Text>{description}</Text>
        </Box>

        {/*
        <Text
          textAlign={['center', 'right']}
          width={['100%', 'auto']}
          pt={1}
          fontSize={[1, 2, 3]}
        >
          <CollectionStats
            limit={limit}
            skip={skip}
            productsNum={products.length}
          />
        </Text>
        */}
      </Flex>
      <Flex flexDirection="column" bg="background" mt={"1em"} p={["5vw", "3.5vw"]}>
        <Flex flexDirection={["column", "row"]}>
          <CatalogProducts
            products={products}
            limit={limit}
            skip={skip}
            cartUrl={cartUrl}
          />
        </Flex>
        <Box width={1} key="pagination">
          <Text textAlign="center">
            <Pagination {...props.pageContext} />
          </Text>
        </Box>
      </Flex>
    </Flex>
  );
}

export default CatalogPage;
