import React, { useState } from 'react';
import { Flex, Box, Button, Text } from 'rebass';
import { useStaticQuery, graphql } from "gatsby";
import styled from '@emotion/styled/macro';

import IndividualProduct from './IndividualProductAnimated';
import Pagination from '../../components/Pagination';
import CollectionStats from '../../components/CollectionStats';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import Dropdown from 'react-bootstrap/Dropdown';

const StyledBox = styled(Box)`
  text-align: center;
`

const CollectionDesc = styled(Box)`
  line-height: 1.67;
`

const CollectionTabs = styled(Tabs)`
  border-top: 1px solid #e6e6e6;
  padding: 0 6vw;
`

const SubcollectionTab = styled(Tab)`
  padding: 30px 0;
`

function CatalogPageBlue(props) {
  const { gatsbyImageProps } = props.data.store.siteMetadata.gatsbyStorefrontConfig;
  const { limit, skip, cartUrl } = props.pageContext;
  const { title, handle, description } = props.data.collection.nodes[0];
  let products = [];

  props.data.collection.nodes.forEach((node) => {
    products = [...products, ...node.products];
  });

  const prodType = handle;

  const data = useStaticQuery(graphql`
    {
      tabTitle: allShopifyCollection(filter: {products: {elemMatch: {productType: {in: "body"}}}}, sort: {order: ASC, fields: handle}) {
        nodes {
          title
          handle
          products {
            id
            shopifyId
            handle
            title
            tags
            productType
            fields {
              shopifyThemePath
              firstImage {
                altText
                originalSrc
              }
            }
            availableForSale
            priceRange {
              minVariantPrice {
                amount
                currencyCode
              }
              maxVariantPrice {
                amount
                currencyCode
              }
            }
            variants {
              shopifyId
              availableForSale
              compareAtPrice
              price
            }
          }
        }
      }
      productsAZ: allShopifyProduct(filter: {productType: {eq: "body"}}, sort: {fields: title, order: ASC}) {
        nodes {
          id
          shopifyId
          handle
          title
          tags
          productType
          fields {
            shopifyThemePath
            firstImage {
              altText
              originalSrc
            }
          }
          availableForSale
          priceRange {
            minVariantPrice {
              amount
              currencyCode
            }
            maxVariantPrice {
              amount
              currencyCode
            }
          }
          variants {
            shopifyId
            availableForSale
            compareAtPrice
            price
          }
        }
      }
      productsZA: allShopifyProduct(filter: {productType: {eq: "body"}}, sort: {fields: title, order: DESC}) {
        nodes {
          id
          shopifyId
          handle
          title
          tags
          productType
          fields {
            shopifyThemePath
            firstImage {
              altText
              originalSrc
            }
          }
          availableForSale
          priceRange {
            minVariantPrice {
              amount
              currencyCode
            }
            maxVariantPrice {
              amount
              currencyCode
            }
          }
          variants {
            shopifyId
            availableForSale
            compareAtPrice
            price
          }
        }
      }
      prodPriceLH: allShopifyProduct(filter: {productType: {eq: "body"}}, sort: {fields: priceRange___minVariantPrice___amount, order: ASC}) {
        nodes {
          id
          shopifyId
          handle
          title
          tags
          productType
          fields {
            shopifyThemePath
            firstImage {
              altText
              originalSrc
            }
          }
          availableForSale
          priceRange {
            minVariantPrice {
              amount
              currencyCode
            }
            maxVariantPrice {
              amount
              currencyCode
            }
          }
          variants {
            shopifyId
            availableForSale
            compareAtPrice
            price
          }
        }
      }
      prodPriceHL: allShopifyProduct(filter: {productType: {eq: "body"}}, sort: {fields: priceRange___minVariantPrice___amount, order: DESC}) {
        nodes {
          id
          shopifyId
          handle
          title
          tags
          productType
          fields {
            shopifyThemePath
            firstImage {
              altText
              originalSrc
            }
          }
          availableForSale
          priceRange {
            minVariantPrice {
              amount
              currencyCode
            }
            maxVariantPrice {
              amount
              currencyCode
            }
          }
          variants {
            shopifyId
            availableForSale
            compareAtPrice
            price
          }
        }
      }
    }
  `)

  const [sortSetting, setSortSetting] = useState('Alphabetical, A-Z');

  return (
    <StyledBox pt={['26vw', '19vw', '16vw']} fontSize={['4vw', '2vw', '1.2vw']} sx={{ background: 'url(//cdn.shopify.com/s/files/1/0117/9005/7534/files/bg-pink.jpg)' }}>
      {/* Header */}
      <Box px={['10vw', '8vw', '20vw']} pb={['5vw', null, '3vw']}>
        <Box fontSize={['9vw', '5vw', '4vw']}>
          {title}
        </Box>
        <CollectionDesc>  
          {description}
        </CollectionDesc>
      </Box>

      <Box sx={{ fontSize: ['3vw', '1.5vw', '1vw'] }}>
        {/* Sort Button */}
        <Dropdown>
          <Dropdown.Toggle variant="light" id="dropdown-basic">
            {sortSetting}
          </Dropdown.Toggle>

          <Dropdown.Menu>
            <Dropdown.Item onClick={() => setSortSetting('Alphabetical, A-Z')}>Alphabetical, A-Z</Dropdown.Item>
            <Dropdown.Item onClick={() => setSortSetting('Alphabetical, Z-A')}>Alphabetical, Z-A</Dropdown.Item>
            <Dropdown.Item onClick={() => setSortSetting('Price, low to high')}>Price, low to high</Dropdown.Item>
            <Dropdown.Item onClick={() => setSortSetting('Price, high to low')}>Price, high to low</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
        
        {/* Tabs */}
        <CollectionTabs defaultActiveKey="body">
          {data.tabTitle.nodes.map((tab, index) => {
            return (
              <SubcollectionTab key={index} eventKey={tab.handle} title={tab.title}>
                {/* Products */}
                <Box backgroundColor="background" padding={'3vw'}>
                  <Box>
                    {sortSetting == 'Alphabetical, Z-A'
                      ? data.productsZA.nodes.map((product, index) => {
                        product['cartUrl'] = cartUrl;
                        if (index + 1 > skip && index + 1 <= skip + limit && product.productType == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else if (index + 1 > skip && index + 1 <= skip + limit && product.tags == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else {
                          return '';
                        }
                      })
                      : sortSetting == 'Price, low to high'
                      ? data.prodPriceLH.nodes.map((product, index) => {
                        product['cartUrl'] = cartUrl;
                        if (index + 1 > skip && index + 1 <= skip + limit && product.productType == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else if (index + 1 > skip && index + 1 <= skip + limit && product.tags == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else {
                          return '';
                        }
                      })
                      : sortSetting == 'Price, high to low'
                      ? data.prodPriceHL.nodes.map((product, index) => {
                        product['cartUrl'] = cartUrl;
                        if (index + 1 > skip && index + 1 <= skip + limit && product.productType == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else if (index + 1 > skip && index + 1 <= skip + limit && product.tags == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else {
                          return '';
                        }
                      })
                      : data.productsAZ.nodes.map((product, index) => {
                        product['cartUrl'] = cartUrl;
                        if (index + 1 > skip && index + 1 <= skip + limit && product.productType == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else if (index + 1 > skip && index + 1 <= skip + limit && product.tags == tab.handle) {
                          return (
                            <IndividualProduct key={product.shopifyId} product={product} />
                          ); 
                        } else {
                          return '';
                        }
                      })
                    }
                  </Box>

                  {/* Pagination */}
                  <Box width={1} key="pagination">
                    <Text sx={{ position: 'relative'}}>
                      <Pagination {...props.pageContext} />
                    </Text>
                  </Box>
                </Box>
              </SubcollectionTab>
            )
          })}
        </CollectionTabs>
      </Box>
    </StyledBox>
  );
}

export default CatalogPageBlue;
